<template>
  <x-crud :loading="loading">
    <FormulateInput
      v-if="action === 'create'"
      label="Тип показателя"
      type="radio"
      name="indicatorType"
      v-model="indicatorCurrentType"
      :options="indicatorTypes"
      :disabled="disabled"
    >
    </FormulateInput>
    <x-form
      v-if="indicatorCurrentType === 'default'"
      name="defaultForm"
      v-model="defaultForm"
      :values="defaultForm"
      @submit="onsubmit('defaultForm')"
    >
      <default-form
        :checkboxes="checkboxes"
        :disabled="disabled"
        :formData="defaultForm"
        :getButtonAction="getButtonAction"
      ></default-form>
    </x-form>
    <x-form
      v-if="indicatorCurrentType === 'spec'"
      name="specForm"
      v-model="specForm"
      :values="specForm"
      @submit="onsubmit('specForm')"
    >
      <spec-form
        :getButtonAction="getButtonAction"
        :disabled="disabled"
        :formData="specForm"
      ></spec-form>
    </x-form>
    <x-form
      v-if="indicatorCurrentType === 'specOrg'"
      name="specOrgForm"
      v-model="specOrgForm"
      :values="specOrgForm"
      @submit="onsubmit('specOrgForm')"
    >
      <spec-org-form
        :getButtonAction="getButtonAction"
        :disabled="disabled"
        :formData="specOrgForm"
      ></spec-org-form>
    </x-form>
  </x-crud>
</template>

<script>
import crudMixin from '@/mixins/crudMixin'
import DefaultForm from './forms/default'
import SpecForm from './forms/spec.vue'
import SpecOrgForm from './forms/specOrg.vue'
import cloneDeep from 'lodash.clonedeep'
export default {
  props: ['action', 'id'],
  components: {
    DefaultForm,
    SpecForm,
    SpecOrgForm,
  },
  mixins: [crudMixin],
  data() {
    return {
      defaultForm: {
        checkboxes: [],
      },
      specForm: {},
      specOrgForm: {},
      indicator: null,
      entityName: 'indicator',
      checkboxes: [
        { value: 'degree_participation', label: 'Степень участия' },
        { value: 'document', label: 'Документ' },
        { value: 'url', label: 'Ссылка на видео' },
        { value: 'hint', label: 'Подсказка' },
      ],
      indicatorCurrentType: 'default',
      indicatorTypes: {
        default: 'Показатель',
        spec: 'Спец.показатель',
        specOrg: 'Спец.показатель руководителя орг.',
      },
    }
  },
  methods: {
    async onsubmit(formName) {
      if (this.action === 'read') {
        this.$router.push(`/indicator/edit/${this.id}`)
        return
      }
      let data = cloneDeep(this[formName])
      console.log(data)
      if (formName === 'defaultForm') data = this.saveDefault(data)
      if (formName === 'specForm') data = this.saveSpec(data)
      if (formName === 'specOrgForm') data = this.saveSpecOrg(data)
      try {
        await this.saveEntity(data)
      } catch (e) {
        console.log(e)
      }
    },
    saveDefault(form) {
      const data = { ...form }
      this.checkboxes.map(checkbox => {
        data[checkbox.value] = data.checkboxes.includes(checkbox.value)
      })
      if (!data.checkboxes.includes('hint')) data.hint_text = null
      return data
    },
    saveSpec(form) {
      return {
        ...form,
        special_indicator: true,
      }
    },
    saveSpecOrg(form) {
      return {
        ...form,
        indicator_head_organization: true,
      }
    },
    async getEntities(entity) {
      try {
        await this.$store.dispatch('app/getEntities', {
          entityName: entity,
        })
      } catch (e) {
        console.log(e)
      }
    },
    renderCheckboxes() {
      this.defaultForm.period = `${this.defaultForm.period.id}`
      if (this.defaultForm.hint_text) this.defaultForm.checkboxes.push('hint')

      for (let key in this.defaultForm) {
        if (
          this.checkboxes.some(
            checkbox => checkbox.value === key && this.defaultForm[key],
          )
        )
          this.defaultForm.checkboxes.push(key)
      }
    },
    checkTypeIndicator(form) {
      if (form.special_indicator) {
        this.indicatorCurrentType = 'spec'
        this.specForm = form
        this.specForm.nomination = `${this.specForm.nomination.id}`
        return
      }
      if (form.indicator_head_organization) {
        this.indicatorCurrentType = 'specOrg'
        this.specOrgForm = form
        this.specOrgForm.nomination = `${this.specOrgForm.nomination.id}`
        return
      }
      this.indicatorCurrentType = 'default'
      this.defaultForm = { ...this.defaultForm, ...form }
      this.renderCheckboxes()
    },
  },
  async created() {
    await this.getEntities('nomination')
    await this.getEntities('period')
    if (this.action !== 'create') {
      const form = await this.getEntity(true)
      this.checkTypeIndicator(form)
    }
    this.loading = false
    console.log(this.formData)
  },
}
</script>

